
.registerMain {
  padding: 0.6rem;
}
.registerMain li {
  list-style: none;
  border-bottom: 1px solid #e6e6e6;
}
.registerMain li input.sendCode_f {
  width: 28%;
  float: right;
  color: #999;
  box-sizing: border-box;
  border-radius: 0px;
  -webkit-appearance: none;
  margin-top: 0.3rem;
  border-radius: 5px;
  font-size: 0.24rem;
  height: 0.6rem;
  border-radius: 1rem;
  background: #fff;
  border: 0.01rem solid #999999;
}
.registerMain li input.sendCode_f.getcode {
  color: #b446bd;
  border: 0.01rem solid #b446bd;
}
.registerMain .forgetBtn {
  width: 5.1rem;
  line-height: 0.92rem;
  background: #404042;
  display: block;
  color: #fff;
  text-align: center;
  font-size: 0.34rem;
  border-radius: 5px;
  margin: 0 auto;
  margin-top: 0.8rem;
  height: 0.92rem;
  background: #b446bd;
  border-radius: 0.54rem;
}
.registerMain li input {
  width: 100%;
  height: 1.1rem;
  border: none;
  outline: none;
  font-size: 0.28rem;
}
.registerMain li input.codeNum {
  width: 70%;
}
.phone {
  position: relative;
  color: #333;
  font-size: 0.32rem;
  box-sizing: border-box;
  padding-right: 0.18rem;
  margin-right: 0.18rem;
}
.phone::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  height: 75%;
  width: 1px;
  background: #999;
  -webkit-transform: translateY(25%);
          transform: translateY(25%);
}



